/* variables */
.SUIDeskNavbar {
  --navbarWidth: 95%;
  --navbarHeight: 71px;
  --navbarBgColor: white;

  --logoWidth: 130px;
  --logoFontSize: 32px;
  --logoFontWeight: 500;
  --logoColor: #397fe0;

  --menuItemFontSize: 21px;
  --menuItemFontWeight: 500;
  --menuItemColor: #00798d;
  --menuItemHoverColor: #004f5c;

  --menuItemBorderColor: #004f5c;
  --menuItemBorder-width: 2px;
}

.SUIDeskNavbarSubMenuLink {
  --subMenuItemFontSize: 18px;
  --subMenuItemFontWeight: 500;
  --subMenuItemColor: #00798d;
  --subMenuItemHoverColor: #004f5c;

  --subMenuDropdownBgColor: white;
  --SubMenuDropdownItemActiveBgColor: #edededad;
}

.SUIDeskNavbarLastMenuItem {
  --lastMenuItemBgColor: #004f5c;
  --lastMenuItemColor: white;
  --lastMenuItemHoverColor: whiteSmoke;
}

/* styles */
/* Entire container */
.SUIDeskNavbarContainer {
  width: var(--navbarWidth);
  height: var(--navbarHeight);
  display: flex;
  justify-content: space-between;
  align-items: center;
  overflow: hidden;
}

.SUIDeskNavbar {
  width: 100%;
  display: flex;
  justify-content: center;
  height: var(--navbarHeight);
  background-color: var(--navbarBgColor);
  box-shadow: 0px 6px 15px -6px #dbdbdb;
}

.SUIDeskNavbarContainerBox.ant-menu-horizontal {
  line-height: 0px !important;
  height: 36px;
  margin-top: 32px !important;
}

/* logo */
.SUIDeskNavbar .SUIDeskLogo img {
  width: var(--logoWidth);
  cursor: pointer;
}

.SUIDeskNavbar .SUIDeskLogo {
  display: flex;
  align-items: center;
}

.SUIDeskNavbar .SUIDeskLogo p {
  font-size: var(--logoFontSize);
  font-weight: var(--logoFontWeight);
  margin: 0;
  padding: 0 !important;
  margin-top: -8px;
}

.SUIDeskNavbar .SUIDeskLogo {
  display: flex;
  align-items: center;
}

/* menu link */
.SUIDeskNavbarContainerBox {
  width: 100%;
  color: var(--menuItemColor) !important;
}

.SUIDeskNavbar .ant-menu-light > .ant-menu {
  color: var(--menuItemColor) !important;
}

.SUIDeskNavbar .ant-menu-horizontal {
  border: none;
}

.SUIDeskNavbar .ant-menu-overflow {
  justify-content: flex-end;
  background-color: transparent;
}

.SUIDeskNavbar span.ant-menu-title-content {
  font-size: var(--menuItemFontSize);
  font-weight: var(--menuItemFontWeight);
}

.SUIDeskNavbar .ant-menu-item-selected::after {
  border-bottom: var(--menuItemBorder-width) solid var(--menuItemBorderColor) !important;
  border-radius: 25px;
}

.SUIDeskNavbar .ant-menu-item:hover::after {
  border-bottom: var(--menuItemBorder-width) solid var(--menuItemBorderColor) !important;
  border-radius: 25px;
}

.SUIDeskNavbar .ant-menu-item-selected,
.SUIDeskNavbar .ant-menu-item:hover {
  color: var(--menuItemHoverColor) !important;
}

/* sub-menu links */
.icon {
  font-size: 19px !important;
  margin-left: 2px;
}

.advantage-menu-hovered .icon {
  transition: transform 12s ease-in-out;
}

.SUIDeskNavbar .ant-menu-submenu-title:hover {
  color: var(--menuItemHoverColor) !important;
}

.SUIDeskNavbar .ant-menu-submenu:hover::after {
  border-bottom: var(--menuItemBorder-width) solid var(--menuItemBorderColor) !important;
  border-radius: 25px;
}

.SUIDeskNavbar .ant-menu-submenu-open::after {
  border-bottom: var(--menuItemBorder-width) solid var(--menuItemBorderColor) !important;
  border-radius: 25px;
}

.SUIDeskNavbar .ant-menu-submenu-selected::after {
  border-bottom: var(--menuItemBorder-width) solid var(--menuItemBorderColor) !important;
  border-radius: 25px;
}

.SUIDeskNavbar .ant-menu-submenu-selected > .ant-menu-submenu-title {
  color: var(--menuItemHoverColor) !important;
}

.SUIDeskNavbar .ant-menu-light > .ant-menu {
  color: var(--menuItemColor);
}

.SUIDeskNavbarSubMenuLink {
  color: var(--subMenuItemColor) !important;
  font-weight: var(--subMenuItemFontWeight) !important;
  font-size: var(--subMenuItemFontSize) !important;
  position: absolute;
  top: 24px;
  background-color: var(--subMenuDropdownBgColor);
  border-radius: 5px;
  padding: 3px;
}

.SUIDeskNavbarSubMenuContainer {
  margin-top: -11px !important;
}

.SUIDeskNavbarSubMenuLink .ant-menu-item {
  margin-right: 15px !important;
}

.SUIDeskNavbarSubMenuLink .ant-menu-item:hover {
  color: var(--subMenuItemHoverColor) !important;
  background-color: var(--SubMenuDropdownItemActiveBgColor) !important;
}

.SUIDeskNavbarSubMenuLink .ant-menu-item-group-title {
  padding: 0;
}

.SUIDeskNavbarSubMenuLink .ant-menu-item-selected {
  background-color: var(--SubMenuDropdownItemActiveBgColor) !important;
  color: var(--subMenuItemHoverColor) !important;
}

.SUIDeskNavbarDownArrowIcon {
  font-size: 19px !important;
  position: relative;
  top: 3.5px;
  scale: 1.1;
}

/* last menu item border design */
.SUIDeskNavbarLastMenuItem span {
  background-color: var(--lastMenuItemBgColor) !important;
  border-radius: 25px !important;
  color: var(--lastMenuItemColor);
  padding: 7.5px 16px 10px 17px !important;
}

.SUIDeskNavbarLastMenuItem span:hover {
  background-color: var(--lastMenuItemBgColor) !important;
  opacity: 0.94 !important;
}

.SUIDeskNavbarLastMenuItem.ant-menu-item:hover {
  color: var(--lastMenuItemHoverColor) !important;
}

.SUIDeskNavbarLastMenuItem.ant-menu-item-selected {
  color: var(--lastMenuItemHoverColor) !important;
}
.SUIDeskNavbarLastMenuItem.ant-menu-item::after {
  border-bottom: none !important;
}
.SUIDeskNavbarLastMenuItem.ant-menu-item:hover::after {
  border-bottom: none !important;
}

.SUIDeskNavbarLastMenuItem.ant-menu-item-selected:after {
  border-bottom: none !important;
}

/* media quries */

@media (max-width: 1023.5px) {
  .SUIDeskNavbar {
    display: none;
  }
}
